}

.select-wrapper select {
  display: block;
  width: 100%;
  padding: 1.3rem 2rem;
  -moz-padding-start: calc(1.2rem - 3px);
  color: #3a3a3a;
  color: var(--color12);
  background-color: #ffffff;
  background-color: var(--color16);
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_14___});
  background-repeat: no-repeat;
  background-position: right 1.2rem center;
  background-size: 1.6rem 1.2rem;
  border: 0.1rem solid #ced4da;
  border-radius: 1rem;
  border-radius: var(--border-radius1);
  transition: all 0.4s ease-in-out;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  min-height: 4.8rem;
}

.select-wrapper .select-options {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: #ffffff;
  background: var("--color16");
}

.select-wrapper .select-options div {
  height: 2.4rem;
}

.accordion {
  width: 100%;
}

.panel {
  padding: 3rem;
  overflow: hidden;
  background-color: #efefef;
  background-color: var("--color14");
  border-radius: 1.5rem;
  border-radius: var("--border-radius2");
}
.panel a {
  -webkit-text-decoration: underline;
  text-decoration: underline;
  font-weight: bold;
}

.panel-heading {
  cursor: pointer;
  line-height: 3.6rem;
  vertical-align: middle;
  font-family: "Cabin-Bold", arial, helvetica, sans-serif;
  font-family: var("--cabin-bold");
  position: relative;
}

.panel-heading::after {
  content: url(${___CSS_LOADER_URL_REPLACEMENT_15___});
  display: inline-block;
  width: 2.4rem;
  height: 2.4rem;
  position: absolute;
  right: 0;
  transform: rotate(0deg);
  transition: transform 0.3s ease;
  text-align: center;
  line-height: 2.4rem;
}

.panel-heading.active::after {
  transform: rotate(-180deg);
}

.panel-heading span {
  vertical-align: middle;
  margin-right: 1rem;
}

.panel-content {
  transition: max-height 0.5s ease;
  max-height: 0;
  overflow: hidden;
}

.panel-content.show {
  max-height: 50rem; /* Adjust this value to accommodate your content */
}

html {
  scroll-behavior: smooth;
}

body {
  background: #ffffff;
  background: var(--color16);
}
body a {
  -webkit-text-decoration: none;
  text-decoration: none;
  cursor: pointer;
}
body img,
body video {
  max-width: 100%;
}
body h1,
body h1 * {
  font-family: "Raleway-Regular", arial, helvetica, sans-serif;
  font-family: var(--raleway-regular);
  font-size: 3.2rem;
  line-height: 3.2rem;
  text-transform: uppercase;
  color: green;
}
@media (min-width: 768px) {
  body h1,
  body h1 * {
    font-size: 4.8rem;
    line-height: 4.8rem;
  }
}
body .section h2,
body .section h2 *,
body .container h2,
body .container h2 * {
  color: #000000;
  color: var(--color11);
  font-family: "Raleway-Regular", arial, helvetica, sans-serif;
  font-family: var(--raleway-regular);
  font-size: 2.8rem;
  line-height: 3.2rem;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  body .section h2,
  body .section h2 *,
  body .container h2,
  body .container h2 * {
    font-size: 3.6rem;
    line-height: 4rem;
  }
}
@media (min-width: 1280px) {
  body .section h2,
  body .section h2 *,
  body .container h2,
  body .container h2 * {
    font-size: 4rem;
    line-height: 4.6rem;
  }
}
body h4,
body h4 * {
  color: #000000;
  color: var(--color11);
  font-family: "Raleway-Bold", arial, helvetica, sans-serif;
  font-family: var(--raleway-bold);
  font-size: 2rem;
  font-weight: bolder;
  line-height: 2.4rem;
}
@media (min-width: 768px) {
  body h4,
  body h4 * {
    font-size: 2.8rem;
    line-height: 3.2rem;
  }
}
body h5,
body h5 * {
  font-family: var(--lato-bold);
  font-size: 2rem;
  font-weight: bolder;
  line-height: 2.4rem;
  text-transform: uppercase;
}
body h6,
body h6 * {
  font-family: var(--lato-bold);
  font-size: 1.2rem;
  font-weight: bolder;
  line-height: 1.2rem;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  body h6,
  body h6 * {
    font-size: 1.8rem;
    line-height: 1.8rem;
  }
}
@media (min-width: 1280px) {
  body h6,
  body h6 * {
    font-size: 2.4rem;
    line-height: 2.4rem;
  }
}
body h1,
body h1 *,
body h2,
body h2 *,
body h3,
body h3 *,
body h4,
body h4 *,
body h5,
body h5 *,
body h6,
body h6 * {
  color: var(--color-heading);
}

::-webkit-scrollbar {
  width: 10px;
}

#popular-product-slider::-webkit-scrollbar {
  width: 0;
  height: 8px;
}

::-webkit-scrollbar-thumb {
  background: linear-gradient(to top, #690b0a 0%, transparent 100%);
  background: linear-gradient(to top, var(--color1) 0%, transparent 100%);
  border-radius: 100px;
}

::-webkit-scrollbar-track {
  background: #a6a6aa;
  background: var(--color13);
}

::-webkit-scrollbar-track-piece {
  background: #a6a6aa;
  background: var(--color13);
