@import 'components/navbar';\r\n@import 'components/switch';\r\n@import 'components/button';\r\n@import 'components/text-field';\r\n@import 'components/radio-check';\r\n@import 'components/select';\r\n@import 'components/panel';\r\n\r\nhtml {\r\n    scroll-behavior: smooth;\r\n}\r\n\r\nbody {\r\n    background: var(--color16);\r\n    // ========================\r\n    // Default text styles\r\n    // ========================\r\n    a {\r\n        text-decoration: none;\r\n        cursor: pointer;\r\n    }\r\n\r\n    img,\r\n    video {\r\n        max-width: 100%;\r\n    }\r\n\r\n    // ======================\r\n    // Default heading styles\r\n    // ======================\r\n    h1 {\r\n        &,\r\n        * {\r\n            font-family: var(--raleway-regular);\r\n            font-size: 3.2rem;\r\n            line-height: 3.2rem;\r\n            text-transform: uppercase;\r\n\r\n            @include media-breakpoint-up(md) {\r\n                font-size: 4.8rem;\r\n                line-height: 4.8rem;\r\n            }\r\n            color:green;\r\n        }\r\n    }\r\n\r\n    .section,\r\n    .container {\r\n        h2 {\r\n            &,\r\n            * {\r\n                color: var(--color11);\r\n                font-family: var(--raleway-regular);\r\n                font-size: 2.8rem;\r\n                line-height: 3.2rem;\r\n                text-transform: uppercase;\r\n\r\n                @include media-breakpoint-up(md) {\r\n                    font-size: 3.6rem;\r\n                    line-height: 4rem;\r\n                }\r\n\r\n                @include media-breakpoint-up(xl) {\r\n                    font-size: 4rem;\r\n                    line-height: 4.6rem;\r\n                }\r\n            }\r\n        }\r\n    }\r\n\r\n    // h3 {\r\n\r\n    // }\r\n\r\n    h4 {\r\n        &,\r\n        * {\r\n            color: var(--color11);\r\n            font-family: var(--raleway-bold);\r\n            font-size: 2rem;\r\n            font-weight: bolder;\r\n            line-height: 2.4rem;\r\n\r\n            @include media-breakpoint-up(md) {\r\n                font-size: 2.8rem;\r\n                line-height: 3.2rem;\r\n            }\r\n        }\r\n    }\r\n\r\n    h5 {\r\n        &,\r\n        * {\r\n            font-family: var(--lato-bold);\r\n            font-size: 2rem;\r\n            font-weight: bolder;\r\n            line-height: 2.4rem;\r\n            text-transform: uppercase;\r\n        }\r\n    }\r\n\r\n    h6 {\r\n        &,\r\n        * {\r\n            font-family: var(--lato-bold);\r\n            font-size: 1.2rem;\r\n            font-weight: bolder;\r\n            line-height: 1.2rem;\r\n            text-transform: uppercase;\r\n\r\n            @include media-breakpoint-up(md) {\r\n                font-size: 1.8rem;\r\n                line-height: 1.8rem;\r\n            }\r\n\r\n            @include media-breakpoint-up(xl) {\r\n                font-size: 2.4rem;\r\n                line-height: 2.4rem;\r\n            }\r\n        }\r\n    }\r\n\r\n    h1,\r\n    h2,\r\n    h3,\r\n    h4,\r\n    h5,\r\n    h6 {\r\n        &,\r\n        * {\r\n            color: var(--color-heading);\r\n        }\r\n    }\r\n}\r\n\r\n::-webkit-scrollbar {\r\n    width: 10px;\r\n}\r\n\r\n#popular-product-slider::-webkit-scrollbar {\r\n    width: 0;\r\n    height: 8px;\r\n}\r\n\r\n//\r\n::-webkit-scrollbar-thumb {\r\n    background: linear-gradient(to top, var(--color1) 0%, transparent 100%);\r\n    border-radius: 100px;\r\n}\r\n\r\n::-webkit-scrollbar-track {\r\n    background: var(--color13);\r\n}\r\n\r\n::-webkit-scrollbar-track-piece {\r\n    background: var(--color13);\r\n}\r\n",":root {\r\n    // Color variables\r\n\r\n    --color1: #690b0a; /* Red */\r\n    --color2: #e1cece; /* Light Pink */\r\n    --color3: #cc0100; /* Dark Red */\r\n    --color4: #f5cccc; /* Pale Pink */\r\n    --color5: #b0016a; /* Magenta */\r\n    --color6: #efcce1; /* Light Magenta */\r\n    --color7: #fd8d0b; /* Orange */\r\n    --color8: #af48ff; /* Purple */\r\n    --color9: #cc9300; /* Dark Orange */\r\n    --color10: #cc3d00; /* Deep Orange */\r\n    --color11: #000000; /* Black */\r\n    --color12: #3a3a3a; /* Dark Gray */\r\n    --color13: #a6a6aa; /* Light Gray */\r\n    --color14: #efefef; /* Gray */\r\n    --color15: #f8f7fa; /* Pale Gray */\r\n    --color16: #ffffff; /* White */\r\n    --color17: #0a87f5; /* Blue */\r\n    --color18: #d8000c; /* Light Red */\r\n    --color19: #227700; /* Dark green */\r\n\r\n    // =====================\r\n    // Typography variables\r\n    // =====================\r\n\r\n    // Raleway variables\r\n    --raleway-light: 'Raleway-Light', arial, helvetica, sans-serif;\r\n    --raleway-regular: 'Raleway-Regular', arial, helvetica, sans-serif;\r\n    --raleway-medium: 'Raleway-Medium', arial, helvetica, sans-serif;\r\n    --raleway-semibold: 'Raleway-SemiBold', arial, helvetica, sans-serif;\r\n    --raleway-bold: 'Raleway-Bold', arial, helvetica, sans-serif;\r\n    --raleway-extrabold: 'Raleway-ExtraBold', arial, helvetica, sans-serif;\r\n\r\n    // Cabin fonts\r\n    --cabin-regular: 'Cabin-Regular', arial, helvetica, sans-serif;\r\n    --cabin-medium: 'Cabin-Medium', arial, helvetica, sans-serif;\r\n    --cabin-semibold: 'Cabin-SemiBold', arial, helvetica, sans-serif;\r\n    --cabin-bold: 'Cabin-Bold', arial, helvetica, sans-serif;\r\n\r\n    // Miscellaneous variables\r\n    --container-width: 124rem; // 1240px\r\n\r\n    --border-radius1: 1rem;\r\n    --border-radius2: 1.5rem;\r\n}\r\n",".navbar {\r\n    .navigation-menu {\r\n        background-color: var(--color16);\r\n        @include media-breakpoint-up('xl') {\r\n            display: flex;\r\n            justify-content: flex-start;\r\n            align-items: center;\r\n        }\r\n\r\n        @include media-breakpoint-down('xl') {\r\n            position: absolute;\r\n            top: 100%;\r\n            left: 0;\r\n            width: 100%;\r\n            transition: all 0.5s ease-in-out;\r\n            overflow-x: hidden;\r\n            overflow-y: auto;\r\n            max-height: 0;\r\n\r\n            &--active {\r\n                max-height: 100vh;\r\n            }\r\n        }\r\n\r\n        .menu {\r\n            display: flex;\r\n            justify-content: center;\r\n            align-items: center;\r\n            height: 4rem;\r\n\r\n            @include media-breakpoint-down('xl') {\r\n                flex-direction: column;\r\n                gap: 1.5rem;\r\n                justify-content: flex-start;\r\n                align-items: flex-start;\r\n                height: 100%;\r\n                padding: 0 0 2rem 0;\r\n            }\r\n\r\n            & > li {\r\n                height: 100%;\r\n                display: flex;\r\n                justify-content: center;\r\n                align-items: center;\r\n                padding: 1rem 2rem;\r\n\r\n                a:not(.no-nav-style),\r\n                span {\r\n                    color: var(--color11);\r\n                    cursor: pointer;\r\n                    font-family: var(--cabin-bold);\r\n                    transition: color 0.5s ease-in-out;\r\n                    display: grid;\r\n                    place-items: center;\r\n                }\r\n\r\n                &.current-menu-item {\r\n                    a {\r\n                        color: var(--color13);\r\n                    }\r\n                }\r\n\r\n                &:hover {\r\n                    & > a {\r\n                        border-bottom: 2px solid var(--color1);\r\n                        margin-bottom: -2px;\r\n                    }\r\n                }\r\n            }\r\n\r\n            .menu-item--active > a {\r\n                border-bottom: 2px solid var(--color1);\r\n                margin-bottom: -2px;\r\n                opacity: 0.5;\r\n            }\r\n        }\r\n    }\r\n}\r\n","@mixin media-breakpoint-up($break-points) {\r\n    @if $break-points == xs {\r\n        @media (min-width: 400px) {\r\n            @content;\r\n        }\r\n    } @else if $break-points == sm {\r\n        @media (min-width: 640px) {\r\n            @content;\r\n        }\r\n    } @else if $break-points == md {\r\n        @media (min-width: 768px) {\r\n            @content;\r\n        }\r\n    } @else if $break-points == lg {\r\n        @media (min-width: 1024px) {\r\n            @content;\r\n        }\r\n    } @else if $break-points == xl {\r\n        @media (min-width: 1280px) {\r\n            @content;\r\n        }\r\n    } @else if $break-points == '2xl' {\r\n        @media (min-width: 1536px) {\r\n            @content;\r\n        }\r\n    } @else {\r\n        @warn \"Breakpoint mixin supports: xs, sm, md, lg, xl, '2xl'\";\r\n    }\r\n}\r\n\r\n@mixin media-breakpoint-down($break-points) {\r\n    @if $break-points == sm {\r\n        @media (max-width: 639px) {\r\n            @content;\r\n        }\r\n    } @else if $break-points == md {\r\n        @media (max-width: 767px) {\r\n            @content;\r\n        }\r\n    } @else if $break-points == lg {\r\n        @media (max-width: 1023px) {\r\n            @content;\r\n        }\r\n    } @else if $break-points == xl {\r\n        @media (max-width: 1279px) {\r\n            @content;\r\n        }\r\n    } @else if $break-points == 2xl {\r\n        @media (max-width: 1535px) {\r\n            @content;\r\n        }\r\n    } @else {\r\n        @warn \"Breakpoint mixin supports: sm, md, lg, xl, 2xl\";\r\n    }\r\n}\r\n",".switch {\r\n    .toggle--checkbox {\r\n        display: none;\r\n    }\r\n\r\n    .toggle--checkbox:checked + .toggle--label {\r\n        background: var(--color11);\r\n        border-color: var(--color11);\r\n        /** Change the cloud to stars **/\r\n        /** Change the sun into the moon **/\r\n        /** Show the dimples on the moon **/\r\n    }\r\n\r\n    .toggle--checkbox:checked + .toggle--label .toggle--label-background {\r\n        left: 60px;\r\n        width: 5px;\r\n    }\r\n\r\n    .toggle--checkbox:checked + .toggle--label .toggle--label-background:before {\r\n        width: 5px;\r\n        height: 5px;\r\n        top: -25px;\r\n    }\r\n\r\n    .toggle--checkbox:checked + .toggle--label .toggle--label-background:after {\r\n        width: 5px;\r\n        height: 5px;\r\n        left: -30px;\r\n        top: 20px;\r\n    }\r\n\r\n    .toggle--checkbox:checked + .toggle--label:before {\r\n        //background: var(--white);\r\n        //border-color: var(--gray-border);\r\n        animation-name: switch;\r\n        animation-duration: 500ms;\r\n        animation-fill-mode: forwards;\r\n        background: var(--color16);\r\n    }\r\n\r\n    .toggle--checkbox:checked + .toggle--label:after {\r\n        transition-delay: 350ms;\r\n        opacity: 1;\r\n    }\r\n\r\n    .toggle--label {\r\n        /** Placeholder element, starting at blue **/\r\n        width: 5.5rem;\r\n        height: 3rem;\r\n        background: var(--color13);\r\n        border-radius: 999px;\r\n        display: flex;\r\n        position: relative;\r\n        transition: all 500ms ease-in;\r\n        cursor: pointer;\r\n        /** The sun cloud and moon stars **/\r\n        /** Sun/Moon element **/\r\n        /** Gray dots on the moon **/\r\n    }\r\n\r\n    .toggle--label:before {\r\n        animation-name: reverse;\r\n        animation-duration: 500ms;\r\n        animation-fill-mode: forwards;\r\n        transition: all 500ms ease-in;\r\n        content: '';\r\n        width: 2.2rem;\r\n        height: 2.2rem;\r\n        position: absolute;\r\n        border-radius: 50%;\r\n        background: var(--color1);\r\n        top: 50%;\r\n        transform: translateY(-50%);\r\n    }\r\n\r\n    @keyframes switch {\r\n        0% {\r\n            left: 4px;\r\n        }\r\n        60% {\r\n            left: 60%;\r\n        }\r\n        100% {\r\n            left: calc(100% - 2.5rem);\r\n        }\r\n    }\r\n    @keyframes reverse {\r\n        0% {\r\n            right: 4px;\r\n        }\r\n        60% {\r\n            right: 60%;\r\n        }\r\n        100% {\r\n            right: calc(100% - 2.5rem);\r\n        }\r\n    }\r\n}\r\n","@mixin btn-primary {\r\n    display: flex;\r\n    justify-content: center;\r\n    align-items: center;\r\n    padding: 1.5rem 2.5rem;\r\n    border-radius: var(--border-radius2);\r\n    background-color: var(--color1);\r\n    color: var(--color16);\r\n    font-size: 1.6rem;\r\n    line-height: 2.4rem;\r\n    font-family: var(--cabin-bold);\r\n    text-decoration: none;\r\n    cursor: pointer;\r\n    transition: all 0.5s ease-in-out;\r\n    border: 0.2rem solid var(--color1);\r\n}\r\n\r\n.button-primary {\r\n    @include btn-primary;\r\n\r\n    &:hover {\r\n        background-color: transparent;\r\n        color: var(--color1);\r\n    }\r\n}\r\n\r\n.button-primary-sm {\r\n    @include btn-primary;\r\n\r\n    &:hover {\r\n        background-color: transparent;\r\n        color: var(--color1);\r\n    }\r\n\r\n    border-radius: var(--border-radius1);\r\n    font-family: var(--cabin-regular);\r\n    padding: 1.8rem 1.2rem;\r\n    height: 3.2rem;\r\n}\r\n\r\n.button2 {\r\n    @include btn-primary;\r\n\r\n    &:hover {\r\n        background-color: transparent;\r\n        border: 0.2rem solid var(--color16);\r\n        color: var(--color16);\r\n    }\r\n}\r\n\r\n.button3 {\r\n    align-items: center;\r\n    border: 0.2rem solid transparent;\r\n    border-radius: var(--border-radius2);\r\n    color: var(--color16);\r\n    cursor: pointer;\r\n    display: flex;\r\n    font-family: var(--cabin-bold);\r\n    font-size: 1.6rem;\r\n    justify-content: center;\r\n    line-height: 2.4rem;\r\n    padding: 1.5rem 2.5rem;\r\n    text-decoration: none;\r\n    transition: all 0.5s ease-in-out;\r\n\r\n    &:hover {\r\n        background-color: transparent;\r\n        border: 0.2rem solid var(--color16);\r\n        color: var(--color16);\r\n    }\r\n}\r\n\r\n.button4 {\r\n    align-items: center;\r\n    border: 0.2rem solid transparent;\r\n 